<template>
    <div>
        <a-layout>
            <a-layout-content  style="flex: 0 0 900px;min-width:900px;max-width: 900px;width: 900px;">
                <a-form :form="productRoutineForm2">
                    <a-form-item label="单位"  :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入单位" v-model="productRoutineForm2.productUnit" />
                    </a-form-item>
                    <a-form-item label="标准" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入标准" v-model="productRoutineForm2.productStandard" />
                    </a-form-item>
                    <a-form-item label="长度" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入长度" v-model="productRoutineForm2.productLength" />
                    </a-form-item>
                    <a-form-item label="直径" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入直径" v-model="productRoutineForm2.productDiameter" />
                    </a-form-item>
                    <a-form-item label="印记" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入印记" v-model="productRoutineForm2.productImprint" />
                    </a-form-item>
                    <a-form-item label="品牌" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入品牌" v-model="productRoutineForm2.productBrand" />
                    </a-form-item>
                    <a-form-item label="材料" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入材料" v-model="productRoutineForm2.productMaterial" />
                    </a-form-item>
                    <a-form-item label="材料说明" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入材料说明" v-model="productRoutineForm2.productMaterialRemark" />
                    </a-form-item>
                    <a-form-item label="牙型" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入牙型" v-model="productRoutineForm2.productToothType" />
                    </a-form-item>
                    <a-form-item label="牙距" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入牙距" v-model="productRoutineForm2.productToothPitch" />
                    </a-form-item>
                    <a-form-item label="表面处理" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入表面处理" v-model="productRoutineForm2.productSurface" />
                    </a-form-item>
                    <a-form-item label="表面处理内容" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入表面处理内容" v-model="productRoutineForm2.productSurfaceRemark" />
                    </a-form-item>
                    <a-form-item label="千支重" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入千支重" v-model="productRoutineForm2.productWeight" />
                    </a-form-item>
                    <a-form-item label="备注" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="输入备注" v-model="productRoutineForm2.remark" />
                    </a-form-item>
                </a-form>
            </a-layout-content>
            <a-layout-sider style="background:none;height:180px;flex: 0 0 200px;"></a-layout-sider>
        </a-layout>
    </div>
</template>
<script>
    import {getProductInfo} from '@/api/ProductApi';
    export default {
        props: ['productId'],
        data() {
            return {
                showPagination: false,
                confirmLoading: false,
                loading: false,
                curproductCode: null, 
                productRoutineForm2:{
                    productBrand: "",        //品牌
                    productDiameter: "",        //直径
                    productCode: null,        //零件id
                    productImprint: "",        //印记
                    productLength: "",        //长度
                    productMaterial: "",        //材料
                    productMaterialRemark: "",        //材料说明
                    productStandard: "",        //标准
                    productSurface: "",        //表面处理
                    productSurfaceRemark: "",        //表面处理说明
                    productToothPitch: "",        //牙距
                    productToothType: "",        //牙型
                    productUnit:"",        //单位
                    productWeight: "",        //千支重
                    remark: "",        //备注
                    routId:null        //常规id更新不能为空
                }
            }
        },
        created(){
            this.getProductInfos(this.productId)
        },
        methods: {
            //获取零部件信息productId
            getProductInfos(corId){
                if(this.productId !== -1){
                     getProductInfo(corId).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                        console.log("获取零部件数据成功",res);
                        this.productRoutineForm2 = res.data.data.productRoutine
                        this.curproductCode = res.data.data.productCode
                        console.log(this.productRoutineForm2)
                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                            console.log("获取零部件数据失败", error);
                    });
                }
                this.productRoutineForm2.productCode = this.curproductCode
            },
        },
    }
</script>

<style scoped>
.ant-form-item-label {text-align: right; vertical-align: middle;line-height: 39.9999px;display: inline-block;
overflow: hidden;overflow-x: hidden; overflow-y: hidden; white-space: nowrap;}
.ant-layout-header {background: none;padding:0px 5px;height: 44px;line-height: 34px;font-size: 16px;margin-top: -10px;}
.title {font-size: 18px;padding: 0px 20px 15px;}
</style>
      

